import { PureComponent, PropTypes, Children } from 'react'

import L10n from './L10n'
import defaultValidationMessages from '../form/i18n/ValidationMsg.json'
import { aauiL10nPropTypes } from './types'


const { string, object, node } = PropTypes
const propTypes = {
  locale: string,
  messages: object,
  children: node,
}
const defaultProps = {
  locale: 'en_US',
}
const childContextTypes = {
  ...aauiL10nPropTypes,
}

export default class L10nProvider extends PureComponent {
  constructor(props, context) {
    super(props, context)

    this.l10n = new L10n(props, context)
  }

  getChildContext() {
    return {
      aauiL10n: this.l10n,
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.locale !== nextProps.locale ||
      this.props.messages !== nextProps.messages) {
      this.l10n.locale = nextProps.locale
      this.l10n.messages = {
        ...this.l10n.messages,
        ...defaultValidationMessages[nextProps.locale],
        ...nextProps.messages,
      }
    }
  }

  render() {
    return Children.only(this.props.children)
  }
}

L10nProvider.propTypes = propTypes
L10nProvider.defaultProps = defaultProps
L10nProvider.childContextTypes = childContextTypes



