import { PHONE_NUMBER_CONFIG } from '../config'

const required = val => !!val
const email = val => /[0-9-._a-z]+@[0-9-.a-z]+\.[a-z]+/.test(val)
const max = (val = '', maxLen = 0) => val.length <= maxLen
const min = (val = '', minLen = 0) => val.length >= minLen
const numberic = val => !isNaN(parseFloat(val)) && isFinite(val)
const url = val => /^((ht|f)tps?):\/\/[\w-]+(\.[\w-]+)+([\w-.,@?^=%&:/~+#]*[\w-@?^=%&/~+#])?$/.test(val)
const phone = val => PHONE_NUMBER_CONFIG.some(phoneNumberPattern => phoneNumberPattern.test(val))

export default {
  required,
  email,
  max,
  min,
  numberic,
  url,
  phone,
}



