import React, { PureComponent, PropTypes } from 'react'
import { cls } from './shared/utils'

import './Tag.less'

const propTypes = {
  className: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
  onCancel: PropTypes.func,
  onClose: PropTypes.func,
  editMode: PropTypes.bool,
  isNew: PropTypes.bool,
}

const defaultProps = {
  editMode: false,
}

export default class Tag extends PureComponent {

  constructor(props) {
    super(props)
    this.state = {
      isInput: false,
      isTabEnter: false,
    }
  }

  convertEscEnterToBlur = e => {
    const target = e.target
    switch (e.which) {
      case 27:
        e.preventDefault()
        this.setState({
          isInputCancel: true,
          isTabEnter: false,
        }, () => {
          target.blur()
        })
        break
      case 9:
      case 13:
        e.preventDefault()
        this.setState({
          isTabEnter: true,
        }, () => {
          target.blur()
        })
        break
      default:
        break
    }
  }

  changeValue = e => {
    const value = e.target.value
    this.setState({
      isInput: false,
    }, () => {
      if (!this.state.isInputCancel) {
        const onChange = this.props.onChange
        if (onChange) onChange(value, this.state.isTabEnter)
      } else {
        const onCancel = this.props.onCancel
        if (onCancel) onCancel()
      }
    })
  }

  toInput = () => {
    this.setState({
      isInput: true,
      isInputCancel: false,
    }, () => {
      const input = this.input
      input.focus()
      input.select()
    })
  }

  toTag = () => {
    this.setState({
      isInput: false,
    })
  }

  render() {
    /* eslint-disable no-unused-vars */
    const { onCancel, className, editMode, onClose, onChange, value, isNew, ...rest } = this.props
    const { isInput } = this.state

    return (
      <span
        className={cls`aaui-tag
                       aaui-tag-${editMode ? 'edit' : 'noedit'}
                       ${className || ''}`}
        {...rest}
      >
        {isInput
          ? <input
            ref={input => { this.input = input }}
            placeholder={isNew ? value : undefined}
            defaultValue={isNew ? undefined : value}
            onKeyDown={this.convertEscEnterToBlur}
            onBlur={this.changeValue}
          />
          : <span onClick={editMode ? this.toInput : undefined}>{value}</span>}
        {editMode && !isInput
          ? <span className='aaui-tag-close aaui-icon-cross' onClick={onClose} />
          : undefined}
      </span>
    )
  }
}

Tag.displayName = 'AAUITag'
Tag.propTypes = propTypes
Tag.defaultProps = defaultProps



