import React, { PureComponent } from 'react'

import connectForm from './connectForm'
import Input from '../Input'
import { FormFieldAPIPropTypes } from './types'

const propTypes = {
  ...FormFieldAPIPropTypes,
}

class TextInput extends PureComponent {
  onBlur = () => {
    const { api: { onValidate } } = this.props

    onValidate(this.input.value)
  }

  setWrappedComponentInstance = input => {
    this.input = input
  }

  render() {
    /* eslint-disable no-unused-vars*/
    const { api: { setValue }, rules, ...rest } = this.props

    return (
      <Input
        onBlur={this.onBlur}
        onChange={e => setValue(e.target.value)}
        ref={this.setWrappedComponentInstance}
        type='text'
        {...rest}
      />
    )
  }
}

TextInput.displayName = 'TextInput'
TextInput.propTypes = propTypes

export default connectForm()(TextInput)



