import React, { PropTypes } from 'react'

import parseRules from './validation/parseRules'
import L10nMessage from '../shared/L10nMessage'
import { cls } from '../shared/utils'


const { string, bool, node } = PropTypes

const propTypes = {
  label: string,
  errMsg: string,
  required: bool,
  rules: string,
  children: node,
}

/* eslint-disable jsx-a11y/label-has-for */
const HFormFieldLayout = ({ label, errMsg, required, children, rules = '' }) => {
  const parsedRules = parseRules(rules)
  const l10nMessageValues = parsedRules.reduce((r, rule) => ({
    ...r,
    [rule.name]: rule.param,
  }), {})

  return (
    <div
      className={cls`form__group grid
        ${errMsg ? 'form__group--error' : ''}`}
    >
      <label className={cls`form__label grid-u-2-12 ${required ? 'form__label--require' : ''}`}><L10nMessage id={label} /></label>
      <div className='form__control grid-u-6-12'>
        {children}
      </div>
      {
        errMsg &&
          <div className='form__validate grid-u-4-12'>
            <i className='icon-close-circle' /><L10nMessage id={errMsg} values={l10nMessageValues} />
          </div>
      }
    </div>
  )
}

HFormFieldLayout.propTypes = propTypes

export default HFormFieldLayout



