import React, { PureComponent, PropTypes } from 'react'
import 'active.css/less/components/form.less'
import 'active.css/less/components/grid.less'

import Alert from './Alert'
import HFormFieldLayout from './form/HFormFieldLayout'
import createField, { isRegisteredField } from './form/createField'
import Store from './form/store'
import { noop, shallowEqual, filterProps } from './shared/utils'
import { FormPropTypes, FormFieledPropTypes } from './form/types'
import { setError } from './form/module'

const FormDefaultConfig = {
  preSubmit: noop,
  shouldSubmit: () => true,
  onSubmit: noop,
  postSubmit: noop,
  onFail: noop,
  onChange: noop,
  fields: {},
}

export default class Form extends PureComponent {
  constructor(props, context) {
    super(props, context)

    /*
      fields: {
        firstName: {
          defaultValue: 'first name',
          validator: validator,
          rules: '',
          parser: v => v,
          formatter: v => v,
        },
        email: {
          defaultValue: 'defalut@defalut.com',
        },
      },
    */
    // Every form field input is treated as contorlled
    // Convert `defaultValue` to `value`
    const fieldKeys = Object.keys(props.fields)
    const formFieldKeys = Object.keys(FormFieledPropTypes)
    const fields = fieldKeys.reduce((r, name) => ({
      ...r,
      [name]: {
        ...filterProps(props.fields[name], formFieldKeys),
        value: props.fields[name].defaultValue,
      },
    }), {})
    // Sync the `this.state` and `this.store.currentState`
    this.state = {
      fields,
      isValid: true,
    }
    this.store = new Store({
      ...props,
      initialState: fields,
    })
  }

  getChildContext() {
    return {
      aauiFormStore: this.store,
    }
  }

  componentDidMount() {
    this.unsubscribe = this.store.subscribe(this.handleChange)
  }

  componentWillUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe()
      this.unsubscribe = null
    }
  }

  handleSubmit = e => {
    /* eslint-disable */
    e && e.preventDefault && e.preventDefault()

    const validationResults = this.store.tryInvokeValidators()

    validationResults.forEach(r => {
      if (r.errMsg) {
        this.store.dispatch(setError({
          name: r.name,
          value: r.errMsg,
        }))
      }
    })

    const { preSubmit, shouldSubmit, onSubmit, postSubmit, onFail } = this.props
    const fields = this.store.getState()
    const values = {}
    const errors = {}
    const reducerKeys = Object.keys(fields)

    for (let i = 0; i < reducerKeys.length; i++) {
      const key = reducerKeys[i]

      values[key] = fields[key].value
      errors[key] = fields[key].errMsg
    }

    const isValid = Object.keys(fields).every(k => !fields[k].errMsg)

    if (!isValid) {
      return onFail(errors)
    }

    if (shouldSubmit(values, preSubmit(values))) {
      onSubmit(values)
    }

    return postSubmit(values)
  }

  handleChange = () => {
    const fields = this.store.getState()
    const isValid = Object.keys(fields).every(k => !fields[k].errMsg)

    if (!shallowEqual(this.state.fields, fields)) {
      this.setState({ isValid, fields }, () => {
        const { onChange } = this.props

        onChange(fields)
      })
    }
  }

  render() {
    const { children, className = 'form--horizontal' } = this.props
    const { isValid, fields } = this.state

    // Todo: Update the error message
    return (
      <form className={className} onSubmit={this.handleSubmit}>
        {!isValid &&
          <Alert type='danger' noClose>
            <strong>Error</strong>
          </Alert>
        }
        {React.Children.map(children, child => {
          if (isRegisteredField(child.type)) {
            return React.cloneElement(child, {
              ...fields[child.props.name],
            })
          } else {
            return child
          }
        })}
      </form>
    )
  }
}

Form.propTypes = {
  ...FormPropTypes,
}
Form.childContextTypes = {
  aauiFormStore: PropTypes.object.isRequired,
}
Form.defaultProps = {
  ...FormDefaultConfig,
}
Form.displayName = 'AAUIForm'
// Form Fields
Form.Field = createField()
Form.HField = createField(HFormFieldLayout)


/**
 * Provide utility function and lifecycle methods for `Form`
 */
export const createForm = (formConfig = {}) => WrappedComponent => {
  class Connect extends PureComponent {
    render() {
      return (
        <WrappedComponent {...this.props} />
      )
    }
  }

  Connect.defaultProps = {
    ...FormDefaultConfig,
    ...formConfig,
  }

  return Connect
}



