import { PropTypes } from 'react'

const { string, object, func, shape, any } = PropTypes

export const FormPropTypes = {
  onSubmit: func,
  onFail: func,
  onChange: func,
  defaultValues: object,
}

export const FormFieldAPIPropTypes = {
  api: shape({
    setValue: func,
    setError: func,
  }),
}

export const FormFieledPropTypes = {
  rules: string,
  value: any,
  parser: func,
  formatter: func,
  validator: func,
  errMsg: string,
}



