const parseRules = (rules = '') => rules.split('|').reduce((r, rule) => {
  let name
  let param

  // Rule has `:` for `min`, `max`
  if (rule.indexOf(':') !== -1) {
    [name, param] = rule.split(':')
  } else {
    name = rule
  }

  return [
    ...r,
    {
      name,
      param,
    },
  ]
}, [])

export default parseRules



